body {
	font-family: 'Lato', sans-serif;
	color:$gray-dark;
	background-color:$gray-dark;
}
.main {
	padding:60px 0 0 0 !important;
	width:100% !important;
	float:none !important;
	background-color:white;
}

// Custom Icons
