	font-family: 'icomoon';
	src:url('../fonts/icomoon.eot?xhv72k');
	src:url('../fonts/icomoon.eot?xhv72k#iefix') format('embedded-opentype'),
		url('../fonts/icomoon.ttf?xhv72k') format('truetype'),
		url('../fonts/icomoon.woff?xhv72k') format('woff'),
		url('../fonts/icomoon.svg?xhv72k#icomoon') format('svg');
	font-weight: normal;
	font-style: normal;
}

