@media only screen and (min-width : 320px) {

	h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
	    font-family: inherit;
	    font-weight: $font-normal;
	    line-height: normal;
	    color: $midnight-blue;
	    margin-top:0;
	    margin-bottom:1em;
	}
	h1 {
		font-size:24px;
		letter-spacing:-1px;
		font-weight: $font-bold;

	}
	h2 {
		font-size:20px;
		font-weight: $font-bold;
	
	}
	h3 {
		font-size:18px;
		
	}
	h4 {
		font-size:18px;
		margin-bottom:20px;
	}
	h5 {
		font-size:16px;
	}
	p, li {
		color:#666;
		font-weight:$font-light;
		font-size: 16px;
		line-height: 1.5em;
	}
	p, ul, figure {
		margin-bottom:1.63em;
	}
	ul, ol {
		padding-left:20px;
	}
	a {
		color:$peter-river;
		cursor: pointer;
	}
	hr {
	    border-top: 5px solid $clouds;
	}
	a:hover {
		color:$belize-hole;
		text-decoration:none;
	}
	table {
		h4 {
			margin:20px 0; 
		}
	}
	blockquote {
		border-left:$peter-river solid 5px;
		background:$clouds;
		padding:30px;
	}
	figure {
		background:$clouds;
		padding:15px;
		figcaption {
			padding:15px;
		}
	}	
	.h-border {
		border-top:dotted 1px;
		padding-top:20px;
	}
	.modal-content {
		border-radius:0;
	}
	.btn {
		border:none;
		border-radius:0;
	}
	.text-reverse {
		color:white;
	}
	//margins
	.margin-bottom-15 {
		margin-bottom:15px;
	}
	.margin-bottom-30 {
		margin-bottom:30px;
	}
	.margin-bottom-45 {
		margin-bottom:45px;
	}
	.margin-bottom-60 {
		margin-bottom:60px;
	}
	//title borders
	.title-border {
		padding:15px 0;
		border-top: dotted 1px;
		border-bottom: dotted  1px;
	}
	.title-border-bottom {
		padding-bottom:15px;
		border-bottom: dotted  1px;
	}.title-border-top {
		padding-top:15px;
		border-top: dotted 1px;
	}
	//box
	.box-black {
		background-color:rgba($gray-dark, 0.8);
	}	
	.box-white {
		background-color:rgba(white, 0.8);
	}
	.box-text-reverse * {
		color:white;
	}
	.content h1:first-of-type {
		margin-top:0;
	}
	.section-padding h2:first-of-type {
		margin-top:0;
	}
	// Padding
	.section-padding {
		padding:80px 0;
	}
	.page-padding {
		padding-top:90px;
		padding-bottom:60px;
	}
	.padding-15 {
		padding:15px;
	}
	.padding-30 {
		padding:30px;
	}

	/* NO Gutters */


	/* Category Nav */
	.category-nav {
		list-style-type:none;
		margin:0 0 2em 0;
		padding:0;
		h5 {
			margin:0;
			font-size: 16px;
			text-transform: uppercase;
			padding:10px;
			background-color:$asbestos;
			color:white;
		}
		ul {
			list-style-type: none;
			margin:0;
			padding:0;
			li {
				border-bottom:lighten($silver, 10%) solid 1px;
			}
			li:last-child {
				border:none;
			}
			a {
				padding:5px 10px;
				display: block;
			}
			li:hover {
				background-color:$clouds;
			}
			.children li {
				border-top:$silver dotted 1px;
				border-bottom:none;
				margin-left:15px;
			}
		}
	}
	/* Cards */
	.card {
		position: relative;
		border:#e3e3e3 solid 1px;
		background-color:white;
		overflow: hidden;
		.card-banner {
			position: absolute;
			top:0;
			left:0;
			z-index: 10;
			width:100%;
			height: 32px;
			ul {
				list-style-type:none;
				margin:0;
				padding:0;
			}
			li {
				color:white;
				font-weight: $font-normal;
				font-size:13px;
			}
			li:first-child {
				text-transform: uppercase;
				position: absolute;
				top:8px;
				left:15px;
			}
			li:last-child {
				position: absolute;
				top:8px;
				right:15px;
			}
			a {
				color:white;
			}
		}		
		.card-meta, .categories {
			font-size: 13px;
			padding:10px 0;
			display: block;
		}
		.card-image {
			position: relative;
			.card-avatar {
				position: absolute;
				bottom:-30px;
				left:0;
				width:100%;
				text-align: center;
				z-index: 10;
				pointer-events: none;
				.avatar {
					border:white solid 2px;
				}
			}
			a {
				position: absolute;
				top:0;
				left:0;
				height: 100%;
				width: 100%;
				z-index: 5;
			}
		}
		.card-text {
			padding:30px 20px 20px 20px;
			h4 {
				margin:0;
			}
			a {
				color:$midnight-blue;
			}
			a:hover {
				text-decoration: underline;
			}
			p {
				font-size:15px;
				line-height: 1.3em;
			}
		}
		.padding-fallback {
			height:20px;
			width:100%;
			position: absolute;
			bottom:0;
			left:0;
			background:white;
		}
		.event-categories {
			display: block;
			margin-bottom:15px;
			a {
				background-color:$silver;
				font-size:11px;
				text-transform: uppercase;
				color:white;
				padding:5px;
			}
			a:hover {
				text-decoration: none;
			}
		}
	}
	/* Check Mark List */
	ul.check-mark-list {
		list-style-type: none;
		margin:0;
		padding:0;
		li {
			padding:10px 10px 10px 32px;
			margin-bottom:5px;
			position: relative;
		}
		li:before {
			font-family: fontawesome;
			content:"\f058"; 
			position: absolute;
			top:10px;
			left:0px;
			color:$nephritis;
			font-size: 24px;
		}
	}
	/* CTA List */
	ul.cta-list {
		list-style-type: none;
		margin:0 0 1.63em 0;
		padding:0;
		text-align: center;
		li {
			display:inline-block;
			margin-right:10px;
		}
		li:last-child {
			margin:0;
		}
	}
	.img-responsive {
		display: inline-block;
	}
}

