@media only screen and (min-width : 1200px) {
	h1 {
		font-size:50px;
		letter-spacing:-1px;
		font-weight: $font-bold;
	}
	h2 {
		font-size:40px;
		font-weight: $font-bold;
	}
	h3 {
		font-size:32px;
	}
	h4 {
		font-size:20px;
	}
	h5 {
		font-size:18px;
	}
}



